import React from 'react';
import InputNumber from './InputNumber';
const inputNumberStyle = {
    margin: '0 5px',
};
const WeekDay = ({ value, onChange, disabled, locale, }) => {
    const splits = value.split('#');
    const week = splits[0];
    const weekDay = splits[1];
    const notifyChange = (weekStr, weekDayStr) => {
        const s = `${weekStr}#${weekDayStr}`;
        if (onChange)
            onChange(s);
    };
    const handleWeekChange = (v) => {
        notifyChange(`${v}`, weekDay);
    };
    const handleWeekDayChange = (v) => {
        notifyChange(week, `${v}`);
    };
    return (<span>
      {locale.index}
      <InputNumber disabled={disabled} min={1} max={4} style={inputNumberStyle} value={parseInt(week, 10)} onChange={handleWeekChange}/>
      {locale.days}
      <InputNumber disabled={disabled} min={1} max={7} style={inputNumberStyle} value={parseInt(weekDay, 10)} onChange={handleWeekDayChange}/>
    </span>);
};
export default WeekDay;
